* {
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  background-color: #F2EAE0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  height: 100%;
}     

#query {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 60px;
  margin-bottom: 60px;
}

#query > img {
  margin-bottom: 20px;
}

#query > p {
  color : #343434;
}
#survey {
  height: 100%;
  width: 100%;
  padding: 20px 20px 0px 20px;
  align-items: center;
}
.resultss {
   width: 100%;
  /* height: %; */
  align-items: center;
  display: flex;
  justify-content: center;
}
.resultIMG{
  height: 1000px;  
}
#progressBar > p {
    color: #536E5F;
}

#progressContainer {
    display: flex;
    height: 15px;
    width: 100%;
    margin-top: 10px;
    background-color: #9CB6A8;
}

#progressFill {
    height: 100%;
    background-color: #536F5F;
}
